module.exports = angular.module("templates", []).run(["$templateCache", function($templateCache) {$templateCache.put("app/layout/shell.html","<div data-ng-controller=\"Shell as vm\"><div id=\"splash-page\" data-ng-show=\"vm.showSplash\" class=\"dissolve-animation\"><div class=\"page-splash\"><div class=\"page-splash-message\">Modular Demo</div></div></div><header class=\"clearfix\"><div data-ng-include=\"\'app/layout/topnav.html\'\"></div></header><section id=\"content\" class=\"content\"><div data-ng-include=\"\'app/layout/sidebar.html\'\"></div><div data-ng-view=\"\" class=\"shuffle-animation\"></div><div ngplus-overlay=\"\" ngplus-overlay-delay-in=\"50\" ngplus-overlay-delay-out=\"700\" ngplus-overlay-animation=\"dissolve-animation\"><img src=\"assets/images/busy.gif\"><div class=\"page-spinner-message overlay-message\">{{vm.busyMessage}}</div></div></section></div>");
$templateCache.put("app/layout/sidebar.html","<div data-cc-sidebar=\"\" when-done-animating=\"vm.sidebarReady()\" data-ng-controller=\"Sidebar as vm\"><div class=\"sidebar-filler\"></div><div class=\"sidebar-dropdown\"><a href=\"#\">Menu</a></div><div class=\"sidebar-inner\"><div class=\"sidebar-widget\"></div><ul class=\"navi\"><li class=\"nlightblue fade-selection-animation\" data-ng-class=\"vm.isCurrent(r)\" data-ng-repeat=\"r in vm.navRoutes\"><a href=\"#{{r.originalPath}}\" data-ng-bind-html=\"r.settings.content\"></a></li></ul></div></div>");
$templateCache.put("app/layout/topnav.html","<nav class=\"navbar navbar-fixed-top navbar-inverse\"><div class=\"navbar-header\"><a href=\"/\" class=\"navbar-brand\"><span class=\"brand-title\">{{vm.title}}</span></a> <a class=\"btn navbar-btn navbar-toggle\" data-toggle=\"collapse\" data-target=\".navbar-collapse\"><span class=\"icon-bar\"></span> <span class=\"icon-bar\"></span> <span class=\"icon-bar\"></span></a></div><div class=\"navbar-collapse collapse\"><div class=\"pull-right navbar-logo\"><ul class=\"nav navbar-nav pull-right\"><li><a href=\"http://www.johnpapa.net/hottowel-angular\" target=\"_blank\">Created by John Papa</a></li><li class=\"dropdown dropdown-big\"><a href=\"http://www.angularjs.org\" target=\"_blank\"><img src=\"assets/images/AngularJS-small.png\"></a></li><li><a href=\"http://developer.marvel.com/\" target=\"_blank\">Marvel API</a></li></ul></div></div></nav>");}]);